body {
  background-color: red;
  color: blue;
  text-transform: lowercase; }

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin-top: 60px;
}
#app > .wrapper{
    max-width: 980px;
    width:100%;
    margin-left:auto;
    margin-right:auto;
}






































.project-list{
}


.project-item{
    position:relative;
}
img{
    width:100%;
}
h2{
    position:absolute;
    background-color: #FFFFFF;
    font-size:2em;
    left: 0.5em;
    top:-0.3em;
    padding: 0.2em 0.6em;
    text-transform: uppercase;
    vertical-align: center;
}
.date-info{
    position:absolute;
    background-color: #FFFFFF;
    font-size:1em;
    left: 1em;
    top:5em;
    padding: 0.2em 0.6em;
    text-transform: uppercase;
    vertical-align: center;
}
.tag-container{
    position: absolute;
    bottom:1em;
    left:0.8em;
    right:1em;
    font-size:1em;
}
.tag{
    background-color: #FFFFFF;
    color:#000000;
    text-decoration: none;
    margin-right: 1em;
    padding:0.3em 1em;
    font-size: 1em;
    cursor: pointer;
    -webkit-transition:background-color 0.2s, color 0.2s;
    transition:background-color 0.2s, color 0.2s;
    display:inline-block;
}
.tag:hover{
    background-color: #000000;
    color:#FFFFFF;
}
